<template>
 <div>
    <div class="remaining-mines"><span class="remaining-mines-icon">‚ö†Ô∏è</span> –û—Å—Ç–∞–ª–æ—Å—å –º–∏–Ω: {{ remainingMines }}</div>  
    
</div>
<div class="navigation__block">
    <button class="navigation__button" @click="restartGame">üîÑ</button>
    <router-link class="table__button" to="/leaderboard">–¢–∞–±–ª–∏—Ü–∞ –ª–∏–¥–µ—Ä–æ–≤</router-link>   
    <button class="navigation__button" @click="goToSettings">üõ†Ô∏è</button>      
     
 </div>
 
  <div class="game">
    <div class="timer"><span class="timer-icon"><div class="clock">
    <div class="hand"></div>
</div> </span>  <span class="timer-value">{{ timer }}</span></div>  
    <div>
      
      <div class="grid" :style="gridStyle">
        <div 
          v-for="cell in cells" 
          :key="cell.id" 
          :class="['cell', { opened: cell.isOpened, mine: cell.isMine }]" 
          @click="handleCellClick(cell)" 
          @contextmenu.prevent="handleRightClick(cell)"
        >
          <span v-if="cell.isOpened">
            <span v-if="cell.isMine">üí£</span>
            <span v-else :class="getNumberClass(cell.value)">{{ cell.value }}</span>
          </span>
          <span v-if="!cell.isOpened && cell.flagged === 'flag'">üö©</span>
          <span v-if="!cell.isOpened && cell.flagged === 'question'">‚ùì</span>
        </div>
      </div>
    </div>
  </div>
 
</template>

<script>
import { useLeaderboardStore } from '../stores/leaderboard'; // –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ –¥–ª—è —Ç–∞–±–ª–∏—Ü—ã –ª–∏–¥–µ—Ä–æ–≤

export default {
  data() {
    return {
      timer: 0, // –¢–∞–π–º–µ—Ä –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –∏–≥—Ä—ã
      remainingMines: 10, // –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—Å—Ç–∞–≤—à–∏—Ö—Å—è –º–∏–Ω
      cells: [], // –ú–∞—Å—Å–∏–≤ —è—á–µ–µ–∫ –∏–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è
      timerInterval: null, // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞ —Ç–∞–π–º–µ—Ä–∞
      rows: 8, // –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–æ–∫ –≤ –∏–≥—Ä–æ–≤–æ–º –ø–æ–ª–µ
      cols: 8, // –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç–æ–ª–±—Ü–æ–≤ –≤ –∏–≥—Ä–æ–≤–æ–º –ø–æ–ª–µ
      difficulty: 'easy', // –£—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
      windowWidth: window.innerWidth, // –®–∏—Ä–∏–Ω–∞ –æ–∫–Ω–∞ –±—Ä–∞—É–∑–µ—Ä–∞ –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ–≥–æ –¥–∏–∑–∞–π–Ω–∞
      
      };
  },
  
  computed: {
  gridStyle() {
      const width = this.windowWidth; // –ü–æ–ª—É—á–∞–µ–º —à–∏—Ä–∏–Ω—É —ç–∫—Ä–∞–Ω–∞
  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ä–∞–∑–º–µ—Ä —è—á–µ–µ–∫ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —à–∏—Ä–∏–Ω—ã —ç–∫—Ä–∞–Ω–∞
  let cellSize;
  if (width < 340) {
    cellSize = 17; // –î–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  } else if (width < 400) {
    cellSize = 19; // –î–ª—è —Å—Ä–µ–¥–Ω–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  } 
  else if (width < 480) {
    cellSize = 22; // –î–ª—è —Å—Ä–µ–¥–Ω–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  }
  else if (width < 600) {
    cellSize = 28; // –î–ª—è —Å—Ä–µ–¥–Ω–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  } 
  else if (width < 768) {
    cellSize = 34; // –î–ª—è —Å—Ä–µ–¥–Ω–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  } 
  else {
    cellSize = 40; // –î–ª—è –±–æ–ª—å—à–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤
  }

  return {
    gridTemplateColumns: `repeat(${this.cols}, ${cellSize}px)`,
    gridTemplateRows: `repeat(${this.rows}, ${cellSize}px)`,
  };
    }
  },
  created() {
    this.difficulty = this.$route.query.difficulty || 'easy'; // –ü–æ–ª—É—á–∞–µ–º —É—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏ –∏–∑ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤
    this.rows = parseInt(this.$route.query.rows) || 8; // –ü–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–æ–∫
    this.cols = parseInt(this.$route.query.cols) || 8; // –ü–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç–æ–ª–±—Ü–æ–≤
    this.initializeGame(); // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∏–≥—Ä—ã
  },
  methods: {    
    updateWindowWidth() {
    this.windowWidth = window.innerWidth; // –û–±–Ω–æ–≤–ª—è–µ–º —à–∏—Ä–∏–Ω—É –æ–∫–Ω–∞
  },
   // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –≤—Å–µ—Ö —è—á–µ–µ–∫
   revealAllCells() {
    this.cells.forEach(cell => {
      cell.isOpened = true; // –û—Ç–∫—Ä—ã–≤–∞–µ–º –∫–∞–∂–¥—É—é —è—á–µ–π–∫—É
    });
  },
    setDifficulty(difficulty) {
      switch (difficulty) {
        case 'easy':
          this.rows = 8;
          this.cols = 8;
          this.remainingMines = 10;
          break;
        case 'medium':
          this.rows = 16;
          this.cols = 16;
          this.remainingMines = 40;
          break;
        case 'hard':
          this.rows = 32;
          this.cols = 16;
          this.remainingMines = 100;
          break;
        default:
          this.rows = 8;
          this.cols = 8;
          this.remainingMines = 10;
      }
    },
    startTimer() {
  // –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ —Ç–∞–π–º–µ—Ä –Ω–µ –∑–∞–ø—É—â–µ–Ω
  if (!this.timerInterval) {
    this.timerInterval = setInterval(() => {
      this.timer++;
    }, 1000);}
    },
    
    initializeGame() {
  // –°–±—Ä–∞—Å—ã–≤–∞–µ–º —Ç–∞–π–º–µ—Ä –Ω–∞ 0
  this.timer = 0; 
  // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –º–∞—Å—Å–∏–≤ —è—á–µ–µ–∫
  this.cells = []; 
  // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —É—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏ –∏–∑ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤
  this.setDifficulty(this.difficulty); 
  // –ó–∞–ø—É—Å–∫–∞–µ–º —Ç–∞–π–º–µ—Ä
  this.startTimer(); 

  // –ó–∞–ø–æ–ª–Ω—è–µ–º –º–∞—Å—Å–∏–≤ —è—á–µ–µ–∫, —Å–æ–∑–¥–∞–≤–∞—è —è—á–µ–π–∫–∏ —Å –Ω–∞—á–∞–ª—å–Ω—ã–º–∏ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏
  for (let i = 0; i < this.rows * this.cols; i++) {
    this.cells.push({ 
      id: i,            // –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —è—á–µ–π–∫–∏
      isOpened: false,  // –Ø—á–µ–π–∫–∞ –µ—â–µ –Ω–µ –æ—Ç–∫—Ä—ã—Ç–∞
      isMine: false,    // –Ø—á–µ–π–∫–∞ –Ω–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –º–∏–Ω—É
      value: 0,         // –ó–Ω–∞—á–µ–Ω–∏–µ —è—á–µ–π–∫–∏ (–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–æ—Å–µ–¥–Ω–∏—Ö –º–∏–Ω)
      flagged: null     // –§–ª–∞–≥ –¥–ª—è –æ–±–æ–∑–Ω–∞—á–µ–Ω–∏—è, –ø–æ–º–µ—á–µ–Ω–∞ –ª–∏ —è—á–µ–π–∫–∞
    });
  }

  // –†–∞–∑–º–µ—â–∞–µ–º –º–∏–Ω—ã –Ω–∞ –∏–≥—Ä–æ–≤–æ–º –ø–æ–ª–µ
  this.placeMines(); 
  // –û–±–Ω–æ–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏—è —è—á–µ–µ–∫ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏—è –º–∏–Ω
  this.updateCellValues(); 
},
placeMines() {
  let minesPlaced = 0; // –°—á–µ—Ç—á–∏–∫ —Ä–∞–∑–º–µ—â–µ–Ω–Ω—ã—Ö –º–∏–Ω

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —Å–∫–æ–ª—å–∫–æ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö —è—á–µ–µ–∫ (–±–µ–∑ –º–∏–Ω) –Ω–∞ –ø–æ–ª–µ
  const availableCells = this.cells.filter(cell => !cell.isMine).length;

  // –ï—Å–ª–∏ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö —è—á–µ–µ–∫ –º–µ–Ω—å—à–µ, —á–µ–º –æ—Å—Ç–∞–≤—à–∏—Ö—Å—è –º–∏–Ω, –≤—ã—Ö–æ–¥–∏–º –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏
  if (availableCells < this.remainingMines) {
    console.error('–ù–µ–¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –º–µ—Å—Ç–∞ –¥–ª—è —Ä–∞–∑–º–µ—â–µ–Ω–∏—è –º–∏–Ω!'); // –í—ã–≤–æ–¥–∏–º —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ –≤ –∫–æ–Ω—Å–æ–ª—å
    return; // –ó–∞–≤–µ—Ä—à–∞–µ–º –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏
  }

  // –†–∞–∑–º–µ—â–∞–µ–º –º–∏–Ω—ã —Å–ª—É—á–∞–π–Ω—ã–º –æ–±—Ä–∞–∑–æ–º, –ø–æ–∫–∞ –Ω–µ –±—É–¥–µ—Ç —Ä–∞–∑–º–µ—â–µ–Ω–æ –Ω—É–∂–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ
  while (minesPlaced < this.remainingMines) {
    const randomIndex = Math.floor(Math.random() * this.cells.length); // –ì–µ–Ω–µ—Ä–∏—Ä—É–µ–º —Å–ª—É—á–∞–π–Ω—ã–π –∏–Ω–¥–µ–∫—Å
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –ª–∏ —è—á–µ–π–∫–∞ –º–∏–Ω—É
    if (!this.cells[randomIndex].isMine) {
      this.cells[randomIndex].isMine = true; // –†–∞–∑–º–µ—â–∞–µ–º –º–∏–Ω—É –≤ —è—á–µ–π–∫–µ
      minesPlaced++; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Å—á–µ—Ç—á–∏–∫ —Ä–∞–∑–º–µ—â–µ–Ω–Ω—ã—Ö –º–∏–Ω
    }
  }
},
handleCellClick(cell) {
  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –æ—Ç–∫—Ä—ã—Ç–∞ –ª–∏ —è—á–µ–π–∫–∞ –∏–ª–∏ –ø–æ–º–µ—á–µ–Ω–∞ —Ñ–ª–∞–≥–æ–º; –µ—Å–ª–∏ –¥–∞, —Ç–æ –≤—ã—Ö–æ–¥–∏–º –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏
  if (cell.isOpened || cell.flagged) return; 

  // –û—Ç–∫—Ä—ã–≤–∞–µ–º —è—á–µ–π–∫—É
  cell.isOpened = true;

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —è—á–µ–π–∫–∞ –º–∏–Ω–æ–π
  if (cell.isMine) {
    alert('–ò–≥—Ä–∞ –æ–∫–æ–Ω—á–µ–Ω–∞! –í—ã –ø–æ–ø–∞–ª–∏ –Ω–∞ –º–∏–Ω—É!'); // –°–æ–æ–±—â–∞–µ–º –æ –ø–æ–ø–∞–¥–∞–Ω–∏–∏ –Ω–∞ –º–∏–Ω—É
    this.revealMines(); // –û—Ç–∫—Ä—ã–≤–∞–µ–º –≤—Å–µ –º–∏–Ω—ã –Ω–∞ –ø–æ–ª–µ   
    this.revealAllCells(); 
    clearInterval(this.timerInterval);
    return; // –ó–∞–≤–µ—Ä—à–∞–µ–º –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏
  }

  // –°—á–∏—Ç–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω –≤–æ–∫—Ä—É–≥ —Ç–µ–∫—É—â–µ–π —è—á–µ–π–∫–∏
  const mineCount = this.countAdjacentMines(cell.id);
  cell.value = mineCount; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ —è—á–µ–π–∫–∏ (–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–æ—Å–µ–¥–Ω–∏—Ö –º–∏–Ω)

  // –ï—Å–ª–∏ –≤–æ–∫—Ä—É–≥ –Ω–µ—Ç –º–∏–Ω, –æ—Ç–∫—Ä—ã–≤–∞–µ–º —Å–æ—Å–µ–¥–Ω–∏–µ –∫–ª–µ—Ç–∫–∏
  if (mineCount === 0) {
    this.openAdjacentCells(cell.id); // –û—Ç–∫—Ä—ã–≤–∞–µ–º —Å–æ—Å–µ–¥–Ω–∏–µ –∫–ª–µ—Ç–∫–∏, –µ—Å–ª–∏ –º–∏–Ω –Ω–µ—Ç
  }

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –≤—ã–∏–≥—Ä–∞–ª –ª–∏ –∏–≥—Ä–æ–∫
  this.checkForWin();
},
handleRightClick(cell) {
  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –æ—Ç–∫—Ä—ã—Ç–∞ –ª–∏ —è—á–µ–π–∫–∞; –µ—Å–ª–∏ –¥–∞, —Ç–æ –≤—ã—Ö–æ–¥–∏–º –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏
  if (cell.isOpened) return; // –ù–µ –º–µ–Ω—è–µ–º –º–µ—Ç–∫–∏ –Ω–∞ –æ—Ç–∫—Ä—ã—Ç—ã—Ö –∫–ª–µ—Ç–∫–∞—Ö

  // –¶–∏–∫–ª–∏—á–µ—Å–∫–∞—è —Å–º–µ–Ω–∞ –º–µ—Ç–æ–∫: —Ñ–ª–∞–∂–æ–∫, –≤–æ–ø—Ä–æ—Å–∏–∫, –±–µ–∑ –º–µ—Ç–∫–∏
  if (cell.flagged === null) {
    cell.flagged = 'flag'; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ñ–ª–∞–∂–æ–∫
    this.remainingMines--; // –£–º–µ–Ω—å—à–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—Å—Ç–∞–≤—à–∏—Ö—Å—è –º–∏–Ω
  } else if (cell.flagged === 'flag') {
    cell.flagged = 'question'; // –ú–µ–Ω—è–µ–º —Ñ–ª–∞–∂–æ–∫ –Ω–∞ –≤–æ–ø—Ä–æ—Å–∏–∫
    this.remainingMines++;
  } else {
    cell.flagged = null; // –°–Ω–∏–º–∞–µ–º –º–µ—Ç–∫—É    
  }

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º —É—Å–ª–æ–≤–∏—è –ø–æ–±–µ–¥—ã –ø–æ—Å–ª–µ –∏–∑–º–µ–Ω–µ–Ω–∏—è –º–µ—Ç–∫–∏
  this.checkForWin(); 
},
getAdjacentCells(cellId) {
  // –í—ã—á–∏—Å–ª—è–µ–º —Å—Ç—Ä–æ–∫—É –∏ —Å—Ç–æ–ª–±–µ—Ü —Ç–µ–∫—É—â–µ–π —è—á–µ–π–∫–∏ –Ω–∞ –æ—Å–Ω–æ–≤–µ –µ—ë –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä–∞
  const row = Math.floor(cellId / this.cols);
  const col = cellId % this.cols;
  const adjacentCells = []; // –ú–∞—Å—Å–∏–≤ –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–ª–µ—Ç–æ–∫

  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –≤—Å–µ–º —Å–æ—Å–µ–¥–Ω–∏–º —è—á–µ–π–∫–∞–º (–≤–∫–ª—é—á–∞—è –¥–∏–∞–≥–æ–Ω–∞–ª—å–Ω—ã–µ)
  for (let r = row - 1; r <= row + 1; r++) {
    for (let c = col - 1; c <= col + 1; c++) {
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ —Å–æ—Å–µ–¥–Ω—è—è —è—á–µ–π–∫–∞ –≤ –ø—Ä–µ–¥–µ–ª–∞—Ö –ø–æ–ª—è
      if (r >= 0 && r < this.rows && c >= 0 && c < this.cols) {
        const adjacentId = r * this.cols + c; // –í—ã—á–∏—Å–ª—è–µ–º –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–∏
        if (adjacentId !== cellId) { // –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –Ω–µ –¥–æ–±–∞–≤–ª—è–µ–º —Å–∞–º—É —è—á–µ–π–∫—É
          adjacentCells.push(this.cells[adjacentId]); // –î–æ–±–∞–≤–ª—è–µ–º —Å–æ—Å–µ–¥–Ω—é—é —è—á–µ–π–∫—É –≤ –º–∞—Å—Å–∏–≤
        }                
      }
    }
  }
  return adjacentCells; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –º–∞—Å—Å–∏–≤ —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–ª–µ—Ç–æ–∫
},
countAdjacentMines(cellId) {
  // –ü–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–ª–µ—Ç–æ–∫ –¥–ª—è –¥–∞–Ω–Ω–æ–π —è—á–µ–π–∫–∏
  const adjacentCells = this.getAdjacentCells(cellId);
  let mineCount = 0; // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è –ø–æ–¥—Å—á–µ—Ç–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –º–∏–Ω

  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –≤—Å–µ–º —Å–æ—Å–µ–¥–Ω–∏–º –∫–ª–µ—Ç–∫–∞–º
  adjacentCells.forEach((cell) => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —Å–æ—Å–µ–¥–Ω—è—è –∫–ª–µ—Ç–∫–∞ –º–∏–Ω–æ–π
    if (cell.isMine) {
      mineCount++; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Å—á–µ—Ç—á–∏–∫ –º–∏–Ω, –µ—Å–ª–∏ –∫–ª–µ—Ç–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç –º–∏–Ω—É
    }
  });

  return mineCount; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω
},
openAdjacentCells(cellId) {
  // –ü–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–ª–µ—Ç–æ–∫ –¥–ª—è –¥–∞–Ω–Ω–æ–π —è—á–µ–π–∫–∏
  const adjacentCells = this.getAdjacentCells(cellId);
  
  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –≤—Å–µ–º —Å–æ—Å–µ–¥–Ω–∏–º –∫–ª–µ—Ç–∫–∞–º
  adjacentCells.forEach(adjCell => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –æ—Ç–∫—Ä—ã—Ç–∞ –ª–∏ –∫–ª–µ—Ç–∫–∞ –∏ –Ω–µ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω –ª–∏ –Ω–∞ –Ω–µ–π —Ñ–ª–∞–≥
    if (!adjCell.isOpened && !adjCell.flagged) {
      adjCell.isOpened = true; // –û—Ç–∫—Ä—ã–≤–∞–µ–º –∫–ª–µ—Ç–∫—É
      
      // –ï—Å–ª–∏ –∫–ª–µ—Ç–∫–∞ –Ω–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –º–∏–Ω—É
      if (!adjCell.isMine) {
        // –ü–æ–¥—Å—á–∏—Ç—ã–≤–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω –≤–æ–∫—Ä—É–≥ —Å–æ—Å–µ–¥–Ω–µ–π –∫–ª–µ—Ç–∫–∏
        const mineCount = this.countAdjacentMines(adjCell.id);
        adjCell.value = mineCount; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∫–ª–µ—Ç–∫–∏ —Ä–∞–≤–Ω—ã–º –∫–æ–ª–∏—á–µ—Å—Ç–≤—É –º–∏–Ω

        // –ï—Å–ª–∏ –≤–æ–∫—Ä—É–≥ –∫–ª–µ—Ç–∫–∏ –Ω–µ—Ç –º–∏–Ω, —Ä–µ–∫—É—Ä—Å–∏–≤–Ω–æ –æ—Ç–∫—Ä—ã–≤–∞–µ–º —Å–æ—Å–µ–¥–Ω–∏–µ –∫–ª–µ—Ç–∫–∏
        if (mineCount === 0) {
          this.openAdjacentCells(adjCell.id); // –†–µ–∫—É—Ä—Å–∏–≤–Ω—ã–π –≤—ã–∑–æ–≤ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è —Å–æ—Å–µ–¥–Ω–∏—Ö –∫–ª–µ—Ç–æ–∫
        }
      }
    }
  });
},
revealMines() {
  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –≤—Å–µ–º –∫–ª–µ—Ç–∫–∞–º –Ω–∞ –ø–æ–ª–µ
  this.cells.forEach((cell) => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ –∫–ª–µ—Ç–∫–∞ –º–∏–Ω–æ–π
    if (cell.isMine) {
      cell.isOpened = true; // –û—Ç–∫—Ä—ã–≤–∞–µ–º –∫–ª–µ—Ç–∫—É, –µ—Å–ª–∏ –æ–Ω–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç –º–∏–Ω—É
    }
  });
},
checkForWin() {
  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –æ—Ç–∫—Ä—ã—Ç—ã –ª–∏ –≤—Å–µ –∫–ª–µ—Ç–∫–∏ –∏–ª–∏ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω—ã —Ñ–ª–∞–≥–∏ –Ω–∞ –≤—Å–µ—Ö –º–∏–Ω–∞—Ö
  const allCellsOpenedOrFlagged = this.cells.every(cell => cell.isOpened || (cell.flagged === 'flag'));
  
  // –£—Å–ª–æ–≤–∏–µ –≤—ã–∏–≥—Ä—ã—à–∞: –≤—Å–µ –∫–ª–µ—Ç–∫–∏ –æ—Ç–∫—Ä—ã—Ç—ã –∏–ª–∏ –æ—Ç–º–µ—á–µ–Ω—ã —Ñ–ª–∞–≥–∞–º–∏, –∏ –Ω–µ –æ—Å—Ç–∞–ª–æ—Å—å –º–∏–Ω
  if (allCellsOpenedOrFlagged && this.remainingMines === 0) {
    // –ó–∞–ø—Ä–∞—à–∏–≤–∞–µ–º –∏–º—è –∏–≥—Ä–æ–∫–∞ –ø–æ—Å–ª–µ –≤—ã–∏–≥—Ä—ã—à–∞
    const playerName = prompt('–ü–æ–∑–¥—Ä–∞–≤–ª—è–µ–º! –í—ã –≤—ã–∏–≥—Ä–∞–ª–∏! üéâ –í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è:');

    if (playerName) {
      const playerTime = this.timer; // –í—Ä–µ–º—è, –∑–∞—Ç—Ä–∞—á–µ–Ω–Ω–æ–µ –Ω–∞ –∏–≥—Ä—É
      const playerLevel = this.difficulty; // –£—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏
      const store = useLeaderboardStore(); // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ –¥–ª—è —Ç–∞–±–ª–∏—Ü—ã –ª–∏–¥–µ—Ä–æ–≤

      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —Å—É—â–µ—Å—Ç–≤—É–µ—Ç –ª–∏ –º–µ—Ç–æ–¥ addPlayer –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
      if (store && typeof store.addPlayer === 'function') {
        // –î–æ–±–∞–≤–ª—è–µ–º –∏–≥—Ä–æ–∫–∞ –≤ —Ç–∞–±–ª–∏—Ü—É –ª–∏–¥–µ—Ä–æ–≤
        store.addPlayer({ name: playerName, time: playerTime, level: playerLevel });
        alert(`–°–ø–∞—Å–∏–±–æ –∑–∞ –∏–≥—Ä—É, ${playerName}! –í–∞—à–µ –≤—Ä–µ–º—è: ${playerTime} —Å–µ–∫—É–Ω–¥ –Ω–∞ —É—Ä–æ–≤–Ω–µ ${playerLevel}.`);
      } else {
        console.error('–û—à–∏–±–∫–∞: –º–µ—Ç–æ–¥ addPlayer –Ω–µ –Ω–∞–π–¥–µ–Ω –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ.'); // –õ–æ–≥–∏—Ä—É–µ–º –æ—à–∏–±–∫—É, –µ—Å–ª–∏ –º–µ—Ç–æ–¥ –Ω–µ –Ω–∞–π–¥–µ–Ω
      }
    } else {
      alert('–í—ã –Ω–µ –≤–≤–µ–ª–∏ –∏–º—è. –ò–≥—Ä–∞ –Ω–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∞.'); // –£–≤–µ–¥–æ–º–ª—è–µ–º, –µ—Å–ª–∏ –∏–º—è –Ω–µ –≤–≤–µ–¥–µ–Ω–æ      
    }

    clearInterval(this.timerInterval); // –û—Å—Ç–∞–Ω–æ–≤–∫–∞ —Ç–∞–π–º–µ—Ä–∞ –ø–æ—Å–ª–µ –≤—ã–∏–≥—Ä—ã—à–∞
  }
},

  
    restartGame() {
      this.timer = 0;
      this.startTimer();
      this.initializeGame(); // –ü–µ—Ä–µ–∑–∞–ø—É—Å–∫ –∏–≥—Ä—ã
    },
    goToSettings() {
      this.$router.push('/settings'); // –ü–µ—Ä–µ—Ö–æ–¥ –∫ –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º
    },
    getNumberClass(value) {
      switch (value) {
        case 1: return 'number-1'; // –°–∏–Ω–∏–π
        case 2: return 'number-2'; // –ó–µ–ª–µ–Ω—ã–π
        case 3: return 'number-3'; // –ö—Ä–∞—Å–Ω—ã–π
        case 4: return 'number-4'; // –¢—ë–º–Ω–æ-—Å–∏–Ω–∏–π
        case 5: return 'number-5'; // –ö–æ—Ä–∏—á–Ω–µ–≤—ã–π
        case 6: return 'number-6'; // –ë–∏—Ä—é–∑–æ–≤—ã–π
        case 7: return 'number-7'; // –ß—ë—Ä–Ω—ã–π
        case 8: return 'number-8'; // –ë–µ–ª—ã–π
        default: return '';
      }
    },
    updateCellValues() {
  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –∫–∞–∂–¥–æ–π –∫–ª–µ—Ç–∫–µ –≤ –º–∞—Å—Å–∏–≤–µ cells
  this.cells.forEach(cell => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–µ —è–≤–ª—è–µ—Ç—Å—è –ª–∏ –∫–ª–µ—Ç–∫–∞ –º–∏–Ω–æ–π
    if (!cell.isMine) {
      // –û–±–Ω–æ–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∫–ª–µ—Ç–∫–∏, –ø–æ–¥—Å—á–∏—Ç—ã–≤–∞—è –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–æ—Å–µ–¥–Ω–∏—Ö –º–∏–Ω
      cell.value = this.countAdjacentMines(cell.id); // –û–±–Ω–æ–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∫–ª–µ—Ç–∫–∏
    }
  });
},
    changeDifficulty(newDifficulty) {
      this.difficulty = newDifficulty; // –û–±–Ω–æ–≤–ª—è–µ–º —É—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏
      this.restartGame(); // –ü–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞–µ–º –∏–≥—Ä—É —Å –Ω–æ–≤—ã–º–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏
    }
  },
  mounted() {
    this.updateWindowWidth(); // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω–∞—á–∞–ª—å–Ω—É—é —à–∏—Ä–∏–Ω—É –æ–∫–Ω–∞
    window.addEventListener('resize', this.updateWindowWidth); // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞
    this.initializeGame(); // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∏–≥—Ä—ã –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏
  },

  beforeUnmount() { 
    clearInterval(this.timerInterval);
    window.removeEventListener('resize', this.updateWindowWidth); // –£–¥–∞–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ —É–Ω–∏—á—Ç–æ–∂–µ–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞       
  },
};
</script>

