// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Ñ—É–Ω–∫—Ü–∏—é defineStore –∏–∑ –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ Pinia –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
import { defineStore } from 'pinia';

// –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ –¥–ª—è —Ç–∞–±–ª–∏—Ü—ã –ª–∏–¥–µ—Ä–æ–≤ —Å –∏–º–µ–Ω–µ–º 'leaderboard'
export const useLeaderboardStore = defineStore('leaderboard', {
  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
  state: () => ({
    players: [], // –ú–∞—Å—Å–∏–≤ –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –∏–≥—Ä–æ–∫–∞—Ö
  }),
  actions: {
    // –ú–µ—Ç–æ–¥ –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ –∏–≥—Ä–æ–∫–æ–≤ –∏–∑ localStorage
    loadPlayers() {
      // –ü–æ–ª—É—á–∞–µ–º —Å—Ç—Ä–æ–∫—É –¥–∞–Ω–Ω—ã—Ö –∏–∑ localStorage –ø–æ –∫–ª—é—á—É 'leaderboard'
      const storedPlayers = localStorage.getItem('leaderboard');
      
      // –ï—Å–ª–∏ –¥–∞–Ω–Ω—ã–µ —Å—É—â–µ—Å—Ç–≤—É—é—Ç, –ø–∞—Ä—Å–∏–º –∏—Ö –∏–∑ JSON-—Ñ–æ—Ä–º–∞—Ç–∞
      if (storedPlayers) {
        this.players = JSON.parse(storedPlayers); // –ü—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –º–∞—Å—Å–∏–≤—É players –∑–∞–≥—Ä—É–∂–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
      }
    },
    
    // –ú–µ—Ç–æ–¥ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –Ω–æ–≤–æ–≥–æ –∏–≥—Ä–æ–∫–∞ –≤ —Ç–∞–±–ª–∏—Ü—É –ª–∏–¥–µ—Ä–æ–≤
    addPlayer(player) {
      // –î–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤–æ–≥–æ –∏–≥—Ä–æ–∫–∞ –≤ –º–∞—Å—Å–∏–≤ players
      this.players.push(player);
      
      // –°–æ—Ä—Ç–∏—Ä—É–µ–º –º–∞—Å—Å–∏–≤ –∏–≥—Ä–æ–∫–æ–≤ –ø–æ –≤—Ä–µ–º–µ–Ω–∏ (time) –≤ –ø–æ—Ä—è–¥–∫–µ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—è
      this.players.sort((a, b) => a.time - b.time);
      
      // –û–±—Ä–µ–∑–∞–µ–º –º–∞—Å—Å–∏–≤ –¥–æ 10 –ª—É—á—à–∏—Ö –∏–≥—Ä–æ–∫–æ–≤
      this.players = this.players.slice(0, 10);
      
      // –°–æ—Ö—Ä–∞–Ω—è–µ–º –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –∏–≥—Ä–æ–∫–æ–≤ –≤ localStorage –≤ —Ñ–æ—Ä–º–∞—Ç–µ JSON
      localStorage.setItem('leaderboard', JSON.stringify(this.players));
    },
  },
});
