// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
import GameComponent from '../components/GameComponent.vue'; // –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ –ø—É—Ç—å –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π
import SettingsComponent from '../components/SettingsComponent.vue'; // –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ –ø—É—Ç—å –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π
import LeaderboardComponent from '../components/LeaderboardComponent.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: GameComponent, // –≠—Ç–æ –º–æ–∂–µ—Ç –±—ã—Ç—å –≤–∞—à –≥–ª–∞–≤–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
  },
  {
    path: '/game',
    name: 'Game',
    component: GameComponent, // –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è –∏–≥—Ä—ã
  },
  {
    path: '/settings',
    name: 'Settings',
    component: SettingsComponent, // –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è –Ω–∞—Å—Ç—Ä–æ–µ–∫
  },
  {
    path: '/leaderboard',
    name: 'Leaderboard',
    component: LeaderboardComponent,
  },
];

const router = createRouter({
  history: createWebHashHistory(), // –ò–∑–º–µ–Ω–µ–Ω–æ –Ω–∞ hash
  routes,
});

export default router;